none;\n\n      tr th {\n        color: $--color-text-primary;\n        font-weight: 500;\n      }\n\n      thead tr th {\n        background-color: #fff;\n        border: none;\n\n        /* Alinha os textos do header ao topo. */\n        height: 100%;\n        vertical-align: top;\n      }\n    }\n\n    .el-table__body-wrapper {\n      border-top: solid thin #ebeef5;\n      flex: 1;\n      overflow: auto;\n\n      .el-table__body tbody tr td {\n        border-right: none;\n      }\n\n      &.is-scrolling-none {\n        overflow-x: hidden;\n      }\n    }\n\n    .el-table__row {\n      .team-container__table-status {\n        &--warning { color: $--color-warning; }\n        &--danger { color: $--color-danger; }\n        .pointer { cursor: pointer; }\n      }\n\n      .team-container__table-action {\n        opacity: 0;\n        transition: .2s ease-in-out;\n        display: inline-block;\n        font-size: 16px;\n        margin: 0 4px;\n\n        &.el-icon-delete:hover {\n          color: $--color-danger;\n          cursor: pointer;\n        }\n\n        &.el-icon-unlock {\n          color: $--color-primary;\n          cursor: pointer;\n        }\n      }\n\n      &:hover {\n       .team-container__table-action {\n          opacity: 1;\n        }\n      }\n    }\n  }\n\n  .team-container__button{\n    margin: 18px auto 0;\n    width: 400px;\n    max-width: 100%;\n    font-size: 16px;\n    & > span {\n      display: flex;\n      align-items: center;\n      justify-content: center;\n    }\n\n    .team-container__button-icon {\n      width: 16px;\n      height: 16px;\n      margin-right: 12px;\n    }\n  }\n}\n\n.workspace-limit-alert {\n  .el-message-box__header {\n    display: none;\n  }\n}\n\n@media (max-height: 1400px) {\n  .team-container {\n    max-width: 1400px;\n    margin: auto;\n  }\n}\n\n@media (max-width: 900px) {\n  .team-container {\n    .team-container__header {\n      margin-top: 0;\n\n      .team-container__header-input {\n        .el-input__inner {\n